import React from 'react'
import { SidebarContainer,Icon,CloseIcon,SidebarWrapper, SidebarMenu,SidebarRoute,SideBtnWrap,SidebarLink } from './sidebarElements';

const Sidebar = ({isOpen, toggle}) => {
    return (
        <SidebarContainer isOpen={isOpen} onClick={toggle}>
            <Icon onClick={toggle}>
                <CloseIcon />
            </Icon>
            <SidebarWrapper>
                <SidebarMenu>
                    <SidebarLink to="problematica" onClick={toggle}>Insomnio </SidebarLink>
                    <SidebarLink to="como" onClick={toggle}>¿Cómo?</SidebarLink>
                    <SidebarLink to="tecnologia" onClick={toggle}>  ¿Por qué?</SidebarLink>
                    <SidebarLink to="beneficios" onClick={toggle}>Beneficios</SidebarLink>
                </SidebarMenu>
                <SideBtnWrap>
                    <SidebarRoute to="/signin">Ingresar</SidebarRoute>
                </SideBtnWrap>
            </SidebarWrapper>
        </SidebarContainer>
    )
}

export default Sidebar
